.bui-tab-target {
	&:not(.active) {
		margin: 0 !important;
		padding: 0 !important;
		height: 0 !important;
		overflow: hidden;
		visibility: hidden;
	}
}

/* tab-display */
.tab-display {
	position: relative;

	.tab-list {
		margin: 0;
		padding: 0;

		.tab-item {
			list-style: none;
			position: relative;
		}
	}

	&.style-a {
		display: flex;
		
		.tab-list {
			flex-grow: 1;
			display: flex;
		
			.tab-item {
				display: flex;
				justify-content: center;
	
				+.tab-item {
					margin-left: 0.5rem;

					@include respond-above(x-small) {margin-left: 1rem;}
					@include respond-above(small) {margin-left: 1.25rem;}
				}
		
				.tab-name {
					@include font-body(small);

					display: flex;
					justify-content: center;
					align-items: center;
					height: 2.5rem;
					color: $color-base-060;
					white-space: nowrap;
					text-decoration: none;
				}
		
				&.current .tab-name {
					position: relative;
					color: $color-base-090;
					font-weight: 700;
				}
			}
		}
	}

	&.style-b {
		display: flex;
		
		.tab-list {
			flex-grow: 1;
			display: flex;
			box-shadow: inset 0 -0.0625rem 0 0 $color-base-020;
			background-color: #fff;
		
			.tab-item {
				.tab-name {
					@include font-body(small);

					display: flex;
					justify-content: center;
					align-items: center;
					padding: 0 0.25rem;
					height: 3rem;
					color: $color-base-060;
					text-decoration: none;
					white-space: nowrap;

					.info-lamp {
						transform: translate(0.125rem, -0.5em);
					}
				}
		
				&.current .tab-name {
					position: relative;
					font-weight: 700;
					color: $color-primary-080;
	
					&::after {
						content: '';
						display: block;
						position: absolute;
						height: 0.125rem;
						right: 0;
						bottom: 0;
						left: 0;
						background-color: $color-primary-070;
					}
				}
			}
		}

		&.type-a {
			.tab-list {
				display: flex;

				.tab-item {
					display: inline-flex;
					vertical-align: top;

					+.tab-item {
						margin-left: 1rem;
					}
				}
			}
		}

		&.type-b {
			.tab-list {
				display: flex;
				justify-content: center;
				align-items: center;

				.tab-item {
					flex-grow: 1;
					display: flex;
					justify-content: center;
				}
			}
		}

		&.type-c {
			.tab-list {
				display: flex;
				align-items: center;

				.tab-item {
					flex-grow: 1;
					display: flex;
					width: 100%;

					.tab-name {
						flex-grow: 1;
					}
				}
			}
		}
	}

	&.style-c {
		position: relative;
		
		.tab-list {
			flex-grow: 1;
			display: flex;
			box-shadow: inset 0 -0.0625rem 0 0 $color-base-030;
	
			.tab-item {
				+.tab-item {
					margin-left: 0.75rem;

					@include respond-above(x-small) {margin-left: 1.25rem;}
					@include respond-above(small) {margin-left: 1.5rem;}
				}
		
				.tab-name {
					@include font-body(medium);

					display: flex;
					justify-content: center;
					align-items: center;
					padding: 0 0.25rem;
					height: 3rem;
					color: $color-base-060;
					text-decoration: none;
				}
				&.current {
					.tab-name {
						position: relative;
						font-weight: 700;
						color: $color-primary-080;
		
						&::after {
							content: '';
							display: block;
							position: absolute;
							height: 0.1875rem;
							right: 0;
							bottom: 0;
							left: 0;
							background-color: $color-primary-070;
						}
					}
				}
			}
		}
	}

	&.style-d {
		.tab-list {
			display: flex;
			justify-content: center;
			align-items: center;
			box-shadow: inset 0 -0.0625rem 0 0 $color-base-020;
			background-color: #fff;
		
			.tab-item {
				flex-grow: 1;
		
				.tab-name {
					@include font-body(small);

					display: flex;
					justify-content: center;
					align-items: center;
					height: 3rem;
					color: $color-base-060;
					text-decoration: none;
					box-sizing: border-box;
				}
		
				&.current .tab-name {
					color: #fff;
					font-weight: 700;
					background-color: $color-primary-070;
				}
			}
		}
	}

	&.style-e {
		.tab-list {
			display: flex;
			justify-content: center;
			align-items: center;
		
			.tab-item {
				flex-grow: 1;
				width: 100%;
		
				.tab-name {
					@include font-body(small);

					display: flex;
					justify-content: center;
					align-items: center;
					position: relative;
					height: 2.5rem;
					color: $color-base-050;
					text-decoration: none;
					border: 1px solid transparent;
					box-sizing: border-box;
					background-color: $color-base-020;

					&:focus {z-index: 1;}
				}
		
				&.current .tab-name {
					color: $color-primary-080;
					background-color: #fff;
					border-color: $color-primary-070;
				}
	
				&:first-child .tab-name {
					border-top-left-radius: 0.125rem;
					border-bottom-left-radius: 0.125rem;
				}
				&:last-child .tab-name {
					border-top-right-radius: 0.125rem;
					border-bottom-right-radius: 0.125rem;
				}
			}
		}

		&.attr-check {
			.tab-item.current .tab-name::before {
				content: '';
				display: block;
				margin-right: 0.375rem;
				width: 1.5rem;
				height: 1.5rem;
				background-repeat: no-repeat;
				background-position: 0 0;
				background-size: 1.5rem 1.5rem;
				background-image: svgIcon(checkbox-checked, $color-primary-070);
			}
		}
	}

	&.style-f {
		display: flex;
		overflow-x: auto;
		-webkit-overflow-scrolling: touch;
		-ms-overflow-style: none;
		scrollbar-width: none;
		&::-webkit-scrollbar {display: none;}
		
		.tab-list {
			flex-grow: 1;
			display: flex;
		
			.tab-item {
				display: flex;
				height: 2rem;
	
				+ .tab-item {margin-left: 0.5rem;}
		
				.tab-name {
					@include font-body(small);
					
					display: flex;
					align-items: center;
					padding: 0 1rem;
					border-radius: 100em;
					color: $color-base-070;
					white-space: nowrap;
					text-decoration: none;
					border: 1px solid #e8e8e8;
				}
		
				&.current {
					.tab-name {
						position: relative;
						color: #fff;
						background-color: $color-primary-070;
						border-color: $color-primary-070;
					}
				}
			}
		}
	}

	&.style-g {
		.tab-list {
			display: flex;
			flex-wrap: wrap;
			justify-content: flex-start;
			align-items: center;
		
			.tab-item {
				@include font-body(small);

				display: flex;
				align-items: stretch;
				list-style: none;
				width: calc(50% - 0.125rem);

				&:nth-child(even) {margin-left: 0.25rem;}
				&:nth-child(n + 3) {margin-top: 0.25rem;}
		
				.tab-name {
					display: flex;
					align-items: center;
					flex-grow: 1;
					padding: 0.5rem 1rem;
					color: $color-base-070;
					text-decoration: none;
					background-color: $color-base-010;
					border: 1px solid transparent;

					&:focus {
						z-index: 1;
					}
				}
		
				&.current .tab-name {
					color: $color-primary-080;
					background-color: #fff;
					border-color: $color-primary-070;
				}
				&:first-child .tab-name {
					border-top-left-radius: 0.125rem;
					border-bottom-left-radius: 0.125rem;
				}
				&:last-child .tab-name {
					border-top-right-radius: 0.125rem;
					border-bottom-right-radius: 0.125rem;
				}
			}
		}
	}
}

.navi-display {
	margin: 1rem 0;
	display: flex;
	justify-content: center;

	&.style-a.type-a .navi-list {
		display: flex;
		align-items: center;
		margin: 0;
		padding: 0;
	
		.navi-item {
			@include font-body(x-small);

			list-style: none;
			position: relative;

			+.navi-item {
				margin-left: 0.5rem;
				padding-left: calc(0.5rem + 0.0625rem);

				&::before {
					content: '';
					display: block;
					position: absolute;
					top: calc(0.5em - 0.25em);
					left: 0;
					width: 0.0625rem;
					height: 1em;
					background-color: $color-base-020;
				}
			}

			.navi-name {
				color: $color-base-060;

				&:not(:hover) {
					text-decoration: none;
				}
			}
		}
	}
}

/* tab-style*/
.tab-display.attr-scroll {
	left: calc(50% - 50vw);
	width: 100vw;
	overflow-x: auto;
	-webkit-overflow-scrolling: touch;
	-ms-overflow-style: none;
	scrollbar-width: none;
	&::-webkit-scrollbar {display: none;}

	.tab-list {
		padding: 0 0.5rem;

		@include respond-above(x-small) {padding: 0 1rem;}
		@include respond-above(small) {padding: 0 1.25rem;}
	}
}